<template>
  <v-container>
    <v-row
      :align="'center'"
      :justify="'center'"
    >
      <v-stepper
        v-model="e1"
        style="min-width: 900px; max-width: 900px"
      >
        <v-stepper-header>
          <v-stepper-step
            step="1"
            @click="e1 = 1"
          >Общая информация</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step
            step="2"
            @click="e1 = 2"
          >Реквизиты</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step
            step="3"
            @click="e1 = 3"
          >Условия</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step
            step="4"
            @click="e1 = 4"
          >Блоки</v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Наименование"
                  v-model="name"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Город"
                  v-model="city"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Контактное лицо"
                  v-model="clientName"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Телефон"
                  v-model="phone"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="E-mail"
                  v-model="email"
                ></v-text-field>
              </v-col>
            </v-row>

            <v-btn
              color="primary"
              @click="e1 = 2"
            >
              Далее
            </v-btn>

            <v-btn
              to="/"
              text
            >Выход</v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">

            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="ИП (ФИО)"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Адрес регистрации"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Почтовый адрес"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="БИК банка"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Телефон"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="E-mail"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Контактное лицо"
                />
              </v-col>
            </v-row>
            <v-btn
              color="primary"
              @click="e1 = 3"
            >
              Сохранить
            </v-btn>

            <v-btn
              @click="e1 = 3"
              text
            >Назад</v-btn>
          </v-stepper-content>

          <v-stepper-content step="3">
            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Комиссия"
                  v-model="comission"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Сроки оплаты"
                  v-model="days"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Просмотр календаря"
                  v-model="calendar"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Форма бронирования"
                  v-model="bookingForm"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Разрешить статусы"
                  v-model="statusPerm"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
              >
                <v-text-field
                  dense
                  label="Лимиты на продажу"
                  v-model="saleLimit"
                />
              </v-col>
            </v-row>

            <v-btn
              color="primary"
              @click="createSource"
              to="list"
            >
              Сохранить
            </v-btn>

            <v-btn
              text
              to="/"
            >Выход</v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'CreateSource',
    data () {
      return {
        e1: 1,
        name: null,
        clientName: null,
        email: null,
        phone: null,
        city: null,
        bookingForm: null,
        statusPerm: null,
        saleLimit: null,
        comission: null,
        days: null,
        calendar: null,
      }
    },
    methods: {
      createSource () {
        const data = {
          name: this.name,
          clientName: this.clientName,
          email: this.email,
          phone: this.phone,
          city: this.city,
          bookingForm: this.bookingForm,
          statusPerm: this.statusPerm,
          saleLimit: this.saleLimit,
          commSize: this.comission + '%',
          days: this.days,
          blocks: 'Да',
          calendar: this.calendar,
          datebeg: new Date().toDateString(),
          perm: false,
        }
        this.$store.commit('CREATE_SOURCE', data)
      },
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
